const getAutocompleteSuggestions = (query, callback) => {
  let result;
  fetch(
    `https://google-search3.p.rapidapi.com/api/v1/search/q=${query}&num=20`,
    {
      method: "GET",
      headers: {
        "x-rapidapi-key": "2cfc8345e9mshb5f6d9cc440c6aep19345fjsn3b8d76320676",
        "x-rapidapi-host": "google-search3.p.rapidapi.com",
      },
    }
  )
    .then((response) => (response = response.json()))
    .then((data) => {
      result = data.results;
      callback(result);
    })

    .catch((err) => {
      console.error(err);
    });
};

export default getAutocompleteSuggestions;
