import React, { Component } from "react";
import getAutocompleteSuggestions from "./api";
import "./Autocomple.css";
import AutocompleteOption from "./AutocompleteOption";

// function to implement debounce logic 
const debounce = (func, wait) => {
  // timer to keep track of cooldown
  let timeout;

  return (...args) => {
    const later = () => {
      timeout = null;
      // execute function
      func(...args);
    };
    clearTimeout(timeout);
    timeout = setTimeout(later, wait);
  };
};

// logic to implement throttle
const throttle = (callback, delay) => {
  // if executed for first time
  let enableCall = true;

  return function (...args) {
    if (!enableCall) return;

    enableCall = false;
    callback(...args);
    setTimeout(() => {
      enableCall = true;
    }, delay);
  };
};

const throttleHandler = throttle(
  (query, callback) => getAutocompleteSuggestions(query, callback),
  500
);
const debounceHandler = debounce(
  (query, callback) =>
    getAutocompleteSuggestions(query, callback),
  400
);

class Autocomplete extends Component {
  constructor(props) {
    super(props);
    this.state = {
      elements: [],
    };
  }
  renderAutocompleteResults = (results) => {
    let elements = [];
    for (let res of results) {
      elements.push(
        <AutocompleteOption
          key={res.title}
          name={res.title}
          link={res.link}
        ></AutocompleteOption>
      );
    }
    this.setState({ elements: elements });
  };

  handleOnChange = (value) => {
    // if value is empty do not trigger the function
    if (value.trim()) {
      let query = value.replace(/ /g, "+");

      this.props.type === 'throttle' ? throttleHandler(query, this.renderAutocompleteResults) : debounceHandler(query, this.renderAutocompleteResults);
    }
  };

  render() {
    return (
      <React.Fragment>
        <div className="autocomplete">
          <div className="autocomplete__header">Autocomplete using {this.props.type}</div>
          <div className="autocomplete__search">
            <span className="fa fa-search"></span>
            <input
              type="text"
              placeholder="Search.."
              className="autocomplete__input"
              onChange={(e) => {
                this.handleOnChange(e.target.value);
              }}
            ></input>
            <div className="autocomplete__results">{this.state.elements}</div>
          </div>
        </div>

      </React.Fragment>
    );
  }
}

export default Autocomplete;
